import {getSureFinance} from '@/api/bowl'
export default {
  name: 'cBottom',
  props: ['state', 'id'],
  data () {
    return {
      isAllow: true
    }
  },
  methods: {
    handleGo () {
      if (!this.isAllow) {
        return this.$vux.toast.text('请先同意免责声明!')
      }
      this._onSure()
    },
    async _onSure () {
      let params = {
        creditId: this.id
      }
      getSureFinance(params).then(({ data: { code, data = {}, msg } }) => {
        if (code === 1) {
          this.$router.push('/result?type=success')
        }
      }).catch((error) => {
        console.log(error)
      })
    }
  }
}



